<template>

    <div class="row">
      <div class="col-md-12">

        <widget headerText="Ranking">
          <data-table :apiUrl="apiUrl"
                      :tableFields="tableFields"
                      :itemsPerPage="itemsPerPage"
                      :sortFunctions="sortFunctions"
                      :apiMode="apiMode"
                      :paginationPath="paginationPath"></data-table>
        </widget>


      </div>
    </div>

</template>

<script>
  import Widget from '../../vuestic-components/vuestic-widget/VuesticWidget'
  import DataTable from '../../vuestic-components/vuestic-datatable/VuesticDataTable'
//  import BadgeColumn from './BadgeColumn.vue'
 // import Vue from 'vue'
  import FieldsDef from '../../vuestic-components/vuestic-datatable/data/fields-definition'
  import ItemsPerPageDef from '../../vuestic-components/vuestic-datatable/data/items-per-page-definition'

  export default {
    components: {
      DataTable,
      Widget
    },
    name: 'Ranking',
    data () {
      return {
        apiUrl: 'https://vuetable.ratiw.net/api/user',
        apiMode: true,
        tableFields: FieldsDef.tableFields,
        itemsPerPage: ItemsPerPageDef.itemsPerPage,
        sortFunctions: FieldsDef.sortFunctions,
        paginationPath: ''
      }
    }
  }
</script>

<style lang="scss">
  .ui-typography {
    .typo-articles {
      margin-bottom: 6.25rem;
      width: 90%;
    }

    .widget-body {
      padding: 3.75rem 4.7rem 0 2.2rem !important;
    }

    .col-md-6 {
      padding-right: 0;
    }

    .vue-lists ul, ol {
      width: 85%;
    }
  }

</style>



